/*
° Este hook puede ir sin extensión .jsx y sin tener React importado, ya que no utiliza nada de él
° Sólo hace la consulta hecha a DatoCMS y retorna los valores que se requieren para un mejor manejo y menos escritura
*/

import { graphql, useStaticQuery } from "gatsby";

const useSeo = () => {

  const query = useStaticQuery( graphql`
  {
    datoCmsSite {
      globalSeo {
        siteName
        titleSuffix
        fallbackSeo {
          title
          description
        }
      }
    }
  }
`)

  return query.datoCmsSite.globalSeo;

}
export default useSeo;