// ? Componentes
import React from 'react'
// ¿ Routing propio de Gatsby (no necesita de React Router)
import { Link } from 'gatsby';
// ~ Estilos
import styled from '@emotion/styled';

const Navegacion = () => {
  return (
    <Nav>
      <NavLink
        to={ '/' }
        // Se agrega esta clase cuando se encuentra en la página activa
        activeClassName="pagina-actual"
      >Inicio</NavLink>
      <NavLink
        to={ '/nosotros' }
        activeClassName="pagina-actual"
      >Nosotros</NavLink>
    </Nav>
  );
}

const Nav = styled.nav`
  display: flex;
  justify-content: center;
  padding-bottom: 3rem;

  @media (min-width: 768px) {
      padding-bottom: 0;
  }

`;

// Estilo a un componente ya existente (Link importado de gatsby)
const NavLink = styled( Link )`
  color: #FFF;
  font-size: 1.6rem;
  font-weight: 700;
  line-height: 1;
  font-family: 'PT Sans', sans-serif;
  text-decoration: none;
  padding: 1rem;
  margin-right: 1rem;

  /* Para seleccionar el último elemento del tipo */
  &:last-of-type{
    margin-right: 0;
  }

  /* Crear una clase custom */
  &.pagina-actual{
    border-bottom: 0.2rem solid #FFF;
  }
`;

export default Navegacion;